<template>
  <div class="hello">

    <p>score: {{ score }}</p>

    <h1>  {{ v }}<sup>2</sup>{{ b }} {{ c }} </h1>

    <div style="width: 100%; padding-top: 15px; padding-bottom: 15px; background-color: white;">

      <h1>( {{ v }} <span @click="r1 = '_'"> {{ r1 }} </span> <span @click="r2 = '_'"> {{ r2 }} </span> )  ( {{ v }} <span @click="r3 = '_'"> {{ r3 }} </span> <span @click="r4 = '_'"> {{ r4 }} </span> )</h1>


    </div>


    <br>
    <button @click="put_char('7')">7</button>
    <button @click="put_char('8')">8</button>
    <button @click="put_char('9')">9</button>

    <br>
    <button @click="put_char('4')">4</button>
    <button @click="put_char('5')">5</button>
    <button @click="put_char('6')">6</button>
    <br>


    <button @click="put_char('1')">1</button>

    <button @click="put_char('2')">2</button>
    <button @click="put_char('3')">3</button>
    <br>
    <button @click="put_char('-')">-</button>


    <button @click="put_char('0')">0</button>
    <button @click="put_char('+')">+</button>



    <br>
    <button @click="check" style="background-color: green;">Submit</button>


  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
  },
  data: function () {
    return {
      score: 0,
      num_one: 9,
      num_two: 5,
      r1: '_',
      r2: '_',
      r3: '_',
      r4: '_',
      v: 'x',
      w: ''
    }
  },
  computed: {
    b: function () {
      var sum = this.num_one + this.num_two;
      if (sum > 0) {
        return ' + ' + sum + this.v
      } else if (sum < 0) {
        return ' - ' + -sum + this.v
      } else {
        return ''
      }
    },
    c: function () {
      var mult = this.num_one * this.num_two;
      if (mult > 0) {
        return ' + ' + mult
      } else if (mult < 0) {
        return ' - ' + -mult
      } else {
        return ''
      }
    }
  },
  methods: {
    active: function () {
      for (let i = 0; i < 4; i++) {
        if ([this.r1, this.r2, this.r3, this.r4][i] == '_') {
          return i
        }  
      }
      return -1
    },
    put_char: function (value) {
      var tar = this.active()
      switch (tar) {
        case 0:
          this.r1 = value
          break;
        case 1:
          this.r2 = value
          break;
        case 2:
          this.r3 = value
          break;
        case 3:
          this.r4 = value
          break;
        default:
          break;
      }
    },
    valid: function () {
      
      this.r2 = + this.r2
      this.r4 = + this.r4

      if (isNaN(this.r2)){
        return false
      }
      if (isNaN(this.r4)){
        return false
      }

      if (this.r1 == '-'){
        this.r2 *= -1
      } else if (this.r1 != '+'){
        return false
      }
      if (this.r3 == '-'){
        this.r4 *= -1
      } else if (this.r3 != '+'){
        return false
      }
      let b = this.r2 + this.r4
      let c = this.r2 * this.r4
      if ((b == this.num_one + this.num_two) && (c == this.num_one * this.num_two)){
        return true
      } else {
        return false
      }
    },
    check: function () {
      if (this.valid()){
        this.score += 1;
        this.num_one = this.randomvalue()
        this.num_two = this.randomvalue()
        if (Math.random() > 0.5){
          this.num_one *= -1
        }if (Math.random() > 0.5){
          this.num_two *= -1
        }
      }
      this.erase()
    },
    erase: function () {
      this.r1 = '_'
        this.r2 = '_'
        this.r3 = '_'
        this.r4 = '_'
    },
    randomvalue: function () {
      let minn = 1
      let maxn = 9
      return Math.floor(Math.random() * (maxn - minn + 1)) + minn;
    }
  }

}
</script>

